import React, {Component} from 'react';
import './App.css';
import {Link } from "react-router-dom";
import Song from './songs/Since When/Since When.mp4';
import Song2 from './songs/Since When/Im Not Fine.mp4';
import Song3 from './songs/Since When/Praise the King.mp4';
import Song4 from './songs/Since When/Has Love Become Underrated.mp4';
import Song5 from './songs/Since When/Judged by the World.mp4';
import Background from './background.jpg';

class Since extends Component{
  render(){
    return (
      <div>
		{/* Nav */}
    <nav className="navbar navbar-expand-lg left">
                <div className="container-fluid">
                    <Link to="/">
                        <h5 className="navbar-brand left">Home</h5>
                    </Link>
                    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
                        <span className="navbar-toggler-icon"></span>
                    </button>
                    <div className="collapse navbar-collapse" id="navbarNav">
                    <ul className="navbar-nav">
						<li className="nav-item">
                        <Link to="/journey">
                        <h5 className="nav-link active left" aria-current="page">The Journey Begins</h5>
                        </Link>
                        </li> 
						<li className="nav-item">
                        <Link to="/since">
                        <h5 className="nav-link active left" aria-current="page">Since When</h5>
                        </Link>
                        </li>
						<li className="nav-item">
                        <Link to="/without">
                        <h5 className="nav-link active left" aria-current="page">Without You</h5>
                        </Link>
                        </li>
                    </ul>
                </div>
            </div>
        </nav>

    {/* Header */}
    <header id="header">
        <img src={Background} alt="BackgroundImage" />
        <h1 className="centered">Justin Townsend Official</h1>
		<h2 className="headtext">Since When</h2>
	</header>

	{/* About */}
		<div className="left">
			<h2>Since When</h2>
			<p>Since When is my fifth album. The album consist of 5 songs each with the goal to bring a question about a topic (which is the title of each song) to your life.</p>
		</div>

		{/* Main */}
			<div id="main">

				<section className="wrapper style1">
					<div className="inner">
						<header className="align-center">
							<h1>Since When</h1>
						</header>
						<div className="video">
							<div className="video-wrapper">
              <video controls src={Song}></video>
							</div>
						</div>
					</div>
				</section>

				<section className="wrapper style1 center">
					<div className="inner">
						<header className="align-center">
							<h1>I'm Not Fine</h1>
						</header>
						<div className="video">
							<div className="video-wrapper">
              <video controls src={Song2}></video>
							</div>
						</div>
					</div>
				</section>

				<section className="wrapper style1">
					<div className="inner">
						<header className="align-center">
							<h1>Praise the King</h1>
						</header>
						<div className="video">
							<div className="video-wrapper">
              <video controls src={Song3}></video>
							</div>
						</div>
					</div>
				</section>

				<section className="wrapper style1 center">
					<div className="inner">
						<header className="align-center">
							<h1>Has Love Become Underrated</h1>
						</header>
						<div className="video">
							<div className="video-wrapper">
              <video controls src={Song4}></video>
							</div>
						</div>
					</div>
				</section>

				<section className="wrapper style1">
					<div className="inner">
						<header className="align-center">
							<h1>Judged by the World</h1>
						</header>
						<div className="video">
							<div className="video-wrapper">
              <video controls src={Song5}></video>
							</div>
						</div>
					</div>
				</section>
			</div>

		{/* Footer */}
			<footer id="footer">
				<div className="copyright center">
					<ul className="icons">
						<li><a target="blank" href="https://www.youtube.com/channel/UCaDHVTA1pSE8pMsDaj1juSw">YouTube</a></li>
						<li><a target="blank" href="https://www.facebook.com/justin.townsend.7330763">Facebook</a></li>
						<li><a target="blank" href="https://www.instagram.com/im_justin_time/">Instagram</a></li>
					</ul>
					&copy; Untitled. Images: <a href="https://unsplash.com">Coverr</a>.
				</div>
			</footer>
      </div>
    );
  }
}

export default Since;
