import React, {Component} from 'react';
import './App.css';
import {Link } from "react-router-dom";
import Song4 from './songs/The Journey Begins/The Journey Begins.mp4';
import Song5 from './songs/Since When/Since When.mp4';
import Song6 from './songs/Without You/Without You.mp4';
import Background from './background.jpg';

class Home extends Component{
  render(){
    return (
      <div>
		{/* Nav */}
    <nav className="navbar navbar-expand-lg left">
                <div className="container-fluid">
                    <Link to="/">
                        <h5 className="navbar-brand left">Home</h5>
                    </Link>
                    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
                        <span className="navbar-toggler-icon"></span>
                    </button>
                    <div className="collapse navbar-collapse" id="navbarNav">
                    <ul className="navbar-nav">
						<li className="nav-item">
                        <Link to="/journey">
                        <h5 className="nav-link active left" aria-current="page">The Journey Begins</h5>
                        </Link>
                        </li> 
						<li className="nav-item">
                        <Link to="/since">
                        <h5 className="nav-link active left" aria-current="page">Since When</h5>
                        </Link>
                        </li>
						<li className="nav-item">
                        <Link to="/without">
                        <h5 className="nav-link active left" aria-current="page">Without You</h5>
                        </Link>
                        </li>
                    </ul>
                </div>
            </div>
        </nav>

    {/* Header */}
    <header id="header">
        <img src={Background} alt="BackgroundImage" />
        <h1 className="centered">Justin Townsend Official</h1>
	</header>

	{/* About */}
		<div className="left">
			<h2>About Me</h2>
			<p id="paragraph">Hello, my name is Justin Townsend. I started making music in April of 2020 during the Pandemic. My first album (Live in the Moment) came out later in the month with the song, "Live in the Moment" having the most views out of the album. I started making songs because I've always had a passion for music, then when I realized that I could make songs using beats, I feel in love with it.</p>
			<p>To Listen to my first three albums:
			<ul>
				<li>Live in the Moment</li>
				<li>Seek the Kingdom</li>
				<li>The Unknown</li>
			</ul>
				I have a link to my YouTube channelat the bottom of this page, where you can listen free of charge. However my last 3 albums you can find on this webiste, enjoy!
			</p>
		</div>

		{/* Main */}
			<div id="main">
				<section className="wrapper style1">
					<div className="inner">
						<header className="align-center">
							<h1>The Journey Begins</h1>
							<p id="paragraph">The Journey Begins is my fourth album with the song, "The Journey Begins" having tied 1st with most views on the album. To listen to the rest of the album, go all the way to the top and select "The Journey Begins", or just go to my YouTube channel down below.</p>
						</header>
						<div className="video">
							<div className="video-wrapper">
              <video controls src={Song4}></video>
							</div>
						</div>
					</div>
				</section>

				<section className="wrapper style1 center">
					<div className="inner">
						<header className="align-center">
							<h1>Since When</h1>
							<p>Since When is my fifth album. To listen to the rest of the album, go all the way to the top and select "Without You", or just go to my YouTube channel down below.</p>
						</header>
						<div className="video">
							<div className="video-wrapper">
              <video controls src={Song5}></video>
							</div>
						</div>
					</div>
				</section>

				<section className="wrapper style1">
					<div className="inner">
						<header className="align-center">
							<h1>Without You</h1>
							<p id="paragraph">Without You is my sixth and lastest album with the song, "Without You" having the most views on the album. To listen to the rest of the album, go all the way to the top and select "Without You", or just go to my YouTube channel down below.</p>
						</header>
						<div className="video">
							<div className="video-wrapper">
              <video controls src={Song6}></video>
							</div>
						</div>
					</div>
				</section>
			</div>

		{/* Footer */}
			<footer id="footer">
				<div className="copyright center">
					<ul className="icons">
						<li><a target="blank" href="https://www.youtube.com/channel/UCaDHVTA1pSE8pMsDaj1juSw">YouTube</a></li>
						<li><a target="blank" href="https://www.facebook.com/justin.townsend.7330763">Facebook</a></li>
						<li><a target="blank" href="https://www.instagram.com/im_justin_time/">Instagram</a></li>
					</ul>
					&copy; Untitled. Images: <a href="https://unsplash.com">Coverr</a>.
				</div>
			</footer>
      </div>
    );
  }
}

export default Home;
